import React, { useState, useEffect } from "react";
import {
  Main,
  Timeline,
  Expertise,
  Project,
  FilmReview,
  FilmDetails,
  BookReview,
  BookDetails,
  Contact,
  Navigation,
} from "./components";
import { Routes, Route, useMatch, useLocation } from "react-router-dom";
import FadeIn from "./components/FadeIn";
import "./index.scss";
import "./i18n";

function App() {
  const [mode, setMode] = useState<string>("dark");

  const handleModeChange = () => {
    setMode((prevMode) => (prevMode === "dark" ? "light" : "dark"));
  };

  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);

  const isFilmDetailsPage = useMatch("/film-review/:filmId");
  const isBookDetailsPage = useMatch("/book-review/:bookId");

  return (
    <div
      className={`main-container ${
        mode === "dark" ? "dark-mode" : "light-mode"
      }`}
    >
      <Navigation
        parentToChild={{ mode }}
        modeChange={handleModeChange}
        isFilmDetails={isFilmDetailsPage}
        isBookDetails={isBookDetailsPage}
      />
      <FadeIn transitionDuration={700}>
        <Routes>
          <Route
            path="/"
            element={
              <>
                <Main />
                <FilmReview />
                <BookReview />
                <Project />
                <Timeline />
              </>
            }
          />
          <Route
            path="/film-review/:filmId"
            element={<FilmDetails mode={mode} />}
          />
          <Route
            path="/book-review/:bookId"
            element={<BookDetails mode={mode} />}
          />
        </Routes>
      </FadeIn>
    </div>
  );
}

export default App;
