import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import "../assets/styles/Filmdetails.scss";
import ReactMarkdown from "react-markdown";

interface FilmDetailsProps {
  mode: string;
}

function FilmDetails({ mode }: FilmDetailsProps) {
  const { filmId } = useParams();
  const [film, setFilm] = useState<any | null>(null);
  const [content, setContent] = useState("");

  // Fetching filmreviews.json and using process.env.PUBLIC_URL
  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data/filmreviews.json`)
      .then((res) => res.json())
      .then((data) => {
        const filmData = data.find((review: any) => review.id === filmId);
        setFilm(filmData); // Set the film data based on the filmId
      })
      .catch((err) => console.error("Error fetching filmreviews.json:", err));
  }, [filmId]);

  // Dynamically loading the markdown content
  useEffect(() => {
    // Adjusted the path to be relative to the public directory
    fetch(`${process.env.PUBLIC_URL}/contents/${filmId}.md`)
      .then((res) => res.text())
      .then((text) => setContent(text))
      .catch((err) => console.error("Error loading markdown file:", err));
  }, [filmId]);

  return (
    <div
      className={`film-details-container ${
        mode === "dark" ? "dark-mode" : "light-mode"
      }`}
    >
      {film && (
        <div className="film-details-content">
          {/* Top section with film image, title, author, and date */}
          <div className="film-header">
            <img
              className="film-image"
              src={film.img}
              alt={film.title}
              width="100%"
            />
            <div className="film-info">
              <h1>{film.title?.replaceAll("-", " ")}</h1>
              <p className="author-date">{`By ${film.author} | ${film.date}`}</p>
            </div>
          </div>

          {/* Review Content */}
          <div className="blog-content">
            <ReactMarkdown>{content}</ReactMarkdown>
          </div>
        </div>
      )}
    </div>
  );
}

export default FilmDetails;
