import React, { useState, useEffect } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import LightModeIcon from "@mui/icons-material/LightMode";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import {
  Select,
  MenuItem,
  FormControl,
  SelectChangeEvent,
} from "@mui/material";
import { useTheme } from "@mui/material/styles";

const drawerWidth = 240;

function Navigation({
  parentToChild,
  modeChange,
  isFilmDetails,
  isBookDetails,
}: any) {
  const { mode } = parentToChild;
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();

  const [mobileOpen, setMobileOpen] = useState<boolean>(false);
  const [scrolled, setScrolled] = useState<boolean>(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const handleLanguageChange = (event: SelectChangeEvent<string>) => {
    i18n.changeLanguage(event.target.value);
  };

  const navItems = [
    [t("navbar.filmreview"), "filmreview"],
    [t("navbar.bookreview"), "bookreview"],
    [t("navbar.projects"), "projects"],
    [t("navbar.experience"), "history"],
  ];
  const theme = useTheme();

  useEffect(() => {
    const handleScroll = () => {
      const navbar = document.getElementById("navigation");
      if (navbar) {
        const scrolled = window.scrollY > navbar.clientHeight;
        setScrolled(scrolled);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollToSection = (section: string) => {
    const sectionElement = document.getElementById(section);
    if (sectionElement) {
      sectionElement.scrollIntoView({ behavior: "smooth" });
    }
  };

  const drawer = (
    <Box sx={{ textAlign: "center" }} onClick={handleDrawerToggle}>
      <Divider />
      <List>
        {navItems.map((item) => (
          <ListItem key={item[0]} disablePadding>
            <ListItemButton
              sx={{ textAlign: "center" }}
              onClick={() => scrollToSection(item[1])}
            >
              <ListItemText primary={item[0]} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar
        component="nav"
        id="navigation"
        className={`navbar-fixed-top${scrolled ? " scrolled" : ""}`}
      >
        <Toolbar
          className="navigation-bar"
          sx={{ display: "flex", justifyContent: "space-between" }}
        >
          {/* Left Section: Dark Mode & Language Switcher */}
          <Box sx={{ display: "flex", alignItems: "center" }}>
            {mode === "dark" ? (
              <IconButton color="inherit" onClick={() => modeChange()}>
                <LightModeIcon />
              </IconButton>
            ) : (
              <IconButton color="inherit" onClick={() => modeChange()}>
                <DarkModeIcon />
              </IconButton>
            )}
            {/* Language Switcher */}
            <FormControl sx={{ width: "auto" }}>
              <Select
                defaultValue="English"
                value={i18n.language}
                onChange={handleLanguageChange}
                sx={{
                  // Ensures the flag fits well
                  "& .MuiSelect-root": {
                    color: "inherit", // Inherit text color from the input field's color
                  },
                  "& .MuiOutlinedInput-notchedOutline": {
                    border: "none", // Remove the border
                  },
                  "& .MuiSelect-icon": {
                    color: mode === "light" ? "#000" : "#fff", // Make the dropdown icon color change based on mode
                  },
                  "& .MuiInputBase-input": {
                    color: mode === "light" ? "#000" : "#fff", // Target the text inside the select box
                  },
                }}
              >
                <MenuItem value="en">English</MenuItem>
                <MenuItem value="zh"></MenuItem>
                <MenuItem value="ja"></MenuItem>
              </Select>
            </FormControl>
          </Box>

          {/* Right Section: Menu Button & Navigation Links */}
          <Box
            sx={{
              display: { xs: "none", sm: "block" },
              flexGrow: 1,
              textAlign: "right",
            }}
          >
            {isFilmDetails || isBookDetails ? (
              <IconButton color="inherit" edge="start" sx={{ mr: 2 }}>
                <ArrowBackIcon onClick={() => navigate("/")} />
              </IconButton>
            ) : (
              navItems.map((item) => (
                <Button
                  key={item[0]}
                  onClick={() => scrollToSection(item[1])}
                  sx={{ color: "#fff", mx: 1 }}
                >
                  {item[0]}
                </Button>
              ))
            )}
          </Box>

          {/* Mobile View: Menu Icon */}
          <Box sx={{ display: { sm: "none" } }}>
            {isFilmDetails || isBookDetails ? (
              <IconButton
                color="inherit"
                edge="start"
                sx={{ mr: 2 }}
                onClick={() => navigate("/")}
              >
                <ArrowBackIcon />
              </IconButton>
            ) : (
              <IconButton
                color="inherit"
                edge="start"
                onClick={handleDrawerToggle}
                sx={{ mr: 2 }}
              >
                <MenuIcon />
              </IconButton>
            )}
          </Box>
        </Toolbar>
      </AppBar>

      {/* Mobile Navigation Drawer */}
      <nav>
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{ keepMounted: true }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
        >
          {drawer}
        </Drawer>
      </nav>
    </Box>
  );
}

export default Navigation;
