import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import "../assets/styles/Bookdetails.scss";
import ReactMarkdown from "react-markdown";

interface BookDetailsProps {
  mode: string;
}

function BookDetails({ mode }: BookDetailsProps) {
  const { bookId } = useParams();
  const [book, setBook] = useState<any | null>(null);
  const [content, setContent] = useState("");

  // Fetching bookeviews.json and using process.env.PUBLIC_URL
  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data/bookreviews.json`)
      .then((res) => res.json())
      .then((data) => {
        const bookData = data.find((review: any) => review.id === bookId);
        setBook(bookData); // Set the book data based on the bookId
      })
      .catch((err) => console.error("Error fetching bookreviews.json:", err));
  }, [bookId]);

  // Dynamically loading the markdown content
  useEffect(() => {
    // Adjusted the path to be relative to the public directory
    fetch(`${process.env.PUBLIC_URL}/contents/${bookId}.md`)
      .then((res) => res.text())
      .then((text) => setContent(text))
      .catch((err) => console.error("Error loading markdown file:", err));
  }, [bookId]);

  return (
    <div
      className={`book-details-container ${
        mode === "dark" ? "dark-mode" : "light-mode"
      }`}
    >
      {book && (
        <div className="book-details-content">
          {/* Top section with book image, title, author, and date */}
          <div className="book-header">
            <img
              className="book-image"
              src={book.img}
              alt={book.title}
              width="100%"
            />
            <div className="book-info">
              <h1>{book.title?.replaceAll("-", " ")}</h1>
              <p className="author-date">{`By ${book.author} | ${book.date}`}</p>
            </div>
          </div>

          {/* Review Content */}
          <div className="blog-content">
            <ReactMarkdown>{content}</ReactMarkdown>
          </div>
        </div>
      )}
    </div>
  );
}

export default BookDetails;
