import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../assets/styles/Filmreview.scss";
import { useTranslation } from "react-i18next";

interface Review {
  id: string;
  path: string;
  img: string;
}

function FilmReview() {
  const [reviews, setReviews] = useState<Review[]>([]);
  const navigate = useNavigate();
  const { t } = useTranslation();

  useEffect(() => {
    // Fetch reviews.json from public/data
    fetch(`${process.env.PUBLIC_URL}/data/filmreviews.json`)
      .then((res) => {
        if (!res.ok) {
          throw new Error(`HTTP error! status: ${res.status}`);
        }
        return res.json();
      })
      .then((data) => setReviews(data))
      .catch((err) => console.error("Error fetching filmreviews.json:", err));
  }, []);

  const handleRedirect = (path: string) => {
    navigate(path); // Navigate to the film details page
  };

  return (
    <div className="filmreview-container" id="filmreview">
      <h1>{t("filmreview.title")}</h1>
      <div className="filmreview-grid">
        {reviews.map((review) => (
          <div
            key={review.id}
            className="filmreview"
            onClick={() => handleRedirect(review.path)}
          >
            <img
              src={review.img}
              className="zoom"
              alt={`Thumbnail for ${review.id}`}
              width="100%"
            />
          </div>
        ))}
      </div>
    </div>
  );
}

export default FilmReview;
